@import './../../assets/scss/variables';

.nav{
  background-color:  $main-color;
  min-width: 95px;
  padding: 20px;
  border-right: 5px solid $fourth-color;;

  @media screen and (max-width:920px){
    display: flex;
    align-items: center;
    justify-content: space-evenly;
  }
  .item a{
    text-decoration: none;
    color: $fourth-color;
    font-size: 18px;
    font-family: $textButton;
    &.activeLink{
      color: $second-color;
    }
  }
}